import { Injectable } from '@angular/core'
// import {Observable} from 'rxjs/observable';
// import { map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DomSanitizer } from '@angular/platform-browser';

/* 
interface User {
  name: string
} */

@Injectable()
export class DataService {
  constructor(private http: HttpClient,private domSanitizer:DomSanitizer) {}
  baseurl:string = "http://sithivili-sb-server.herokuapp.com/";
  // baseurl:string = "http://localhost:8080/";
  bearer:string = 'Bearer sithivilisbbebt';

  defaultImage = this.domSanitizer.bypassSecurityTrustUrl("data:image/svg+xml;utf8;base64,");
  userImgURL= this.domSanitizer.bypassSecurityTrustUrl("data:image/svg+xml;utf8;base64," )

  logoImgURL = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQUjnDgJ4RLpEfSQxbdrN2ua-hTdDgOQXDMgUNq_6FPWJG6w3DNgw&s";
  
  //SPRING BOOT SERVER REQUESTS
  deleteUser(id:number){
    return this.http.delete(this.baseurl + 'api/users/user/delete/'+id,{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }

  addVolunteer(name:string,password:string,gender:string){
    return this.http.post(this.baseurl +'api/admin/new/volunteer',{
      'username':name,
      'password':password,
      'gender':gender
    },{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }

  signUp(mobile:string,username:string,password:string){
    return this.http.post(this.baseurl+'api/users/user/new',{
      'mobile':mobile,
      'username':username,
      'password':password
    },{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }

  updateUser(id:number,username:string,password:string){
    return this.http.put(this.baseurl+'api/users/user/update/'+id,{
      'username':username,
      'password':password
    },{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }

  login(username:string,password:string){
        return this.http.post(this.baseurl+'api/users/user/login',{
      'username':username,
      'password':password
    },{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }

  loadVolunteers(){
    return this.http.get(this.baseurl+'api/users/volunteers/all',{headers:new HttpHeaders().set('Authorization',this.bearer)});
  }

  getUserById(id:number){
    return this.http.get(this.baseurl+'api/users/user/'+id,{headers:new HttpHeaders().set('Authorization',this.bearer)})
  }
}


